.tablepaging{
  float: right;
  margin: 15px 15px 10px 0px;
}
.tablepaging .pagination{
list-style: none;
display: inline-block;
padding: 0;
}
.tablepaging .pagination li{
display: inline;
text-align: center;
}
.tablepaging .pagination a{
float: left;
display: block;
font-size: 14px;
font-weight: 500;
text-decoration: none;
padding: 5px 12px;
color: #fff;
margin-left: -1px;
border: 1px solid transparent;
line-height: 1.5;
}
.tablepaging .modal{
  position: inherit;
  margin-bottom: 0px;
  min-height: 40px;
  margin-top: 6px;
}
.tablepaging .pagination a:active{
outline: none;
}
.tablepaging .modal a{
margin: 0 5px;
padding: 0;
width: 30px;
height: 30px;
line-height: 30px;
border-radius: 100%;
background-color: #2a7fff;
transform: translateY(0);
transition: all .3s ease-out;
}
.tablepaging .modal a.prev{
border-radius: 15px 0 0 15px;
width: 40px;
}
.tablepaging .modal a.next{
border-radius: 0 15px 15px 0;
width: 40px;
}
.tablepaging .modal a:hover{
background-color: #969696;
color: #fff;
font-weight: 1000;
/*transform: translateY(8px);
transition: all .3s ease-out;*/
}
.tablepaging .modal a.active, .tablepaging .modal a:active{
background-color: #969696;
}

