.overflow-unset {
  overflow: unset !important;
}
.cursour-pointer {
  cursor: pointer;
}
.cursour-not-allowed {
  cursor: not-allowed;
}
.bg-dark-grey {
  background: #babebf !important;
}
.bg-light-grey {
  background: #f9fafb;
}
div[id*="license-text"] {
  display: none !important;
}
.custom-select {
  background-color: white;
  height: 39px;
  width: 100%;
}
.custom-select-thin {
  background-color: white;
  height: 36px;
  font-size: 12px;
  font-family: Poppins, sans-serif !important;
  width: 100%;
  border-radius: 0px;
}
select:not([multiple]) {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-position: right 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  padding: 0.5em;
  padding-right: 1.5em;
}
#myThinSelect {
  border-radius: 0;
}

.ck-editor__editable_inline {
  min-height: 250px;
}

/*modals*/
.custom-modal {
  width: 100vw;
  height: 100vh;
}
/* snackbar */
.snackbar-wrapper {
  border-radius: 4px;
  width: 30vw;
  height: 40px;
  padding: 8px;
  margin-top: 72px;
  color: white;
}
.snackbar-msg {
  margin-left: 4px;
}
.snackbar-closeicon {
  margin-top: 4px;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
/*thumbnails*/
.overlay {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}
/* fonts */
.t-global {
  font-family: Poppins, sans-serif !important;
}
.text-transparent {
