/* Form components*/
input[type="text"] ::placeholder {
  font-family: Poppins, sans-serif;
}
.input-bottom-border {
  background: transparent;
  border-color: transparent !important;
  border-bottom-color: silver !important;
  outline: none;
}
input.md-checkbox {
  width: 20px;
  height: 20px;
