}
/*Graphs*/
.donutchart-legend-dot {
  color: #fff;
  border-radius: 50px;
  width: 18px;
  height: 18px;
  display: inline-block;
  margin: 0px 3px -5px 0px;
}
/*font awesome*/
i.cfa-rounded-circle {
  display: inline-block;
  border-radius: 60px;
  box-shadow: 0px 0px 8px #3f6289;
