/*icons*/
.onlyicon {
  font-size: 18px;
  padding: 15px;
  text-align: center;
  border-bottom: 1px solid #ddd;
  width: 100%;
}

.cashinflowclick,
.cashoutflowclick,
.nonoperatflowclick {
  cursor: pointer;
  color: #2a7fff;
  border: 1px solid #2a7fff;
  padding: 5px;
  margin-left: 10px;
}
.accordion__button td .cashinflowclick {
  display: none;
}
.accordion__button[aria-expanded="true"] td .cashinflowclick {
  display: inline-block;
}

.table-container {
  position: relative;
}

.table-container .loader {
  text-align: center;
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.5);
}

.paynewdetsec {
  border-radius: 5px;
  margin-bottom: 30px;
}
.paynewdetsec label {
  font-weight: 500;
}
.paynewdetsec .subbtn {
  font-size: 14px;
  border: none;
}
.paynewdetsec .subbtn {
  box-shadow: none;
  border: none;
  outline: none;
}
.paynewdetsectran {
  border-radius: 5px;
}
.paynewdetsectran table {
  border-collapse: collapse;
  width: 100%;
}
.paynewdetsectran td,
th {
  text-align: left;
  padding: 8px;
}
.paynewdetsectran th {
  background: linear-gradient(to bottom, #777777 0%, #444444 100%) !important;
  color: #fff;
  font-weight: normal !important;
}
.paynewdetsectran tr:nth-child(even) {
  background-color: #ececec;
}
.paynewdetsectran .titletranshist {
  font-size: 18px;
  margin-top: 5px;
  margin-bottom: 15px;
}
.divider {
  border-top: 1px solid #ddd;
  padding: 15px 0px;
  margin-top: 10px;
}

.total-amount-wrapper {
  padding: 5px 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  position: absolute;
  right: 20px;
  background-color: #fafafa;
}
.total-amount-wrapper.in-col {
  right: -14px !important;
}
.total-amount-wrapper.right-0 {
  right: 0px !important;
}
.custom-table-header i {
  /* margin-top: 10px;
  margin-right: 16px;
  font-size: 20px;
  cursor: pointer; */
}
.custom-table-header .btn{
  margin: 10px 5px 0px 0px;
  padding: 5px;
}
.custom-table-header .custom-button {
  background:#2a7fff !important;
  color: white;
  font-size: 14px;
  padding: 4px;
}
.custom-table-header .custom-button .disabled {
  background: #ccc !important;
}

.inputWithIcon input::placeholder {
  color: #2a7fff;
}

.inputWithIcon {
  position: relative;
}
.inputWithIcon input {
  height: 35px;
}

.inputWithIcon i {
  position: absolute;
  right: 0;
  top: 28px;
  padding: 8px 8px;
  color: #2a7fff;
  transition: 0.3s;
  font-size: 18px;
  height: 35px;
}

.autocomplete.inputWithIcon i {
  top: 0;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.54);
}

.floatedPayIQ {
  /* width: 200px; */
  height: 58px;
  position: sticky;
  top: 190px;
  z-index: 1;
  background: rgba(255, 255, 255, 0.7);
}

.hide-scroll::-webkit-scrollbar {
  width: 0px !important; /* Remove scrollbar space */
  background: transparent !important; /* Optional: just make scrollbar invisible */
}

.hide-scroll {
  scrollbar-width: none;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}


.graph-loader {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-color: rgba(255, 255, 255, 0.8);
    display: flex;
  align-items: center;
  justify-content: center;
}

/* remove x from input edge browser */
input::-ms-clear,
input::-ms-reveal {
  display: none;
}
